// base
$font-family: "Poppins", sans-serif;
$content-width: 1110px;
$container-offset: 15px;
$container-width: calc($content-width + ($container-offset * 2));
$transition: 0.25s ease-in-out;
$menu-transition: 0.35s ease-in-out;

// colors
$white-color: #fff;
$black-color: #000;
$accent-color: #ff1c89;
