*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  height: 100%;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-weight: 600;
  font-size: 20px;
  line-height: 1.5;
  color: #fff;
  background-color: #000;
}

img,
video {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

.main {
  overflow-x: hidden;
}

@media screen and (max-width: 768px) {
  body {
    font-size: 18px;
  }
}
@media screen and (max-width: 575px) {
  body {
    font-size: 17px;
  }
}
@media screen and (max-width: 1024px) {
  .menu {
    position: fixed;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 8;
    display: none;
    flex-direction: column;
    align-items: center;
    overflow: auto;
    padding: 50px 15px;
    width: 100%;
    max-width: 350px;
    background: #1d0259;
    opacity: 0;
    transition: 0.35s ease-in-out;
  }
  .menu.is-active {
    opacity: 1;
  }
  .overlay {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 5;
    display: none;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    transition: 0.35s ease-in-out;
  }
  .overlay.is-active {
    opacity: 1;
  }
}
.nav {
  font-size: 16px;
}
.nav li {
  font-size: 16px;
}
.nav li:hover {
  text-decoration: underline;
}
.nav li.is-active {
  color: #ff1c89;
}

.title {
  font-weight: 800;
  font-size: 40px;
  color: #ff1c89;
}

.subtitle {
  font-weight: 800;
  font-size: 32px;
}

.small-title {
  font-weight: 800;
  font-size: 24px;
  color: #ff1c89;
}

.color-accent {
  color: #ff1c89;
}

.uppercase {
  text-transform: uppercase;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1140px;
}

.text-center {
  text-align: center;
}

.disable-scroll,
.overflow-hidden {
  overflow: hidden;
}
.disable-scroll body,
.overflow-hidden body {
  overflow: hidden;
}

@media screen and (max-width: 768px) {
  .title {
    font-size: 34px;
  }
  .subtitle {
    font-size: 26px;
  }
  .small-title {
    font-size: 20px;
  }
}
@media screen and (max-width: 768px) {
  .title {
    font-size: 28px;
  }
  .subtitle {
    font-size: 22px;
  }
  .small-title {
    font-size: 18px;
  }
}
.burger {
  position: relative;
  display: none;
  border: none;
  padding: 0;
  width: 26px;
  height: 20px;
  color: #fff;
  background-color: transparent;
  cursor: pointer;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: currentcolor;
  transition: transform 0.35s ease-in-out, top 0.35s ease-in-out;
}
.burger::before {
  top: 0;
}
.burger::after {
  top: calc(100% - 3px);
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 3px;
  background-color: currentcolor;
  transform: translateY(-50%);
  transition: transform 0.35s ease-in-out;
}
.burger.is-active {
  z-index: 9;
}
.burger.is-active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger.is-active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger.is-active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

@media screen and (max-width: 1024px) {
  .burger {
    display: block;
  }
}
.socials {
  display: flex;
  align-items: center;
}
.socials li {
  margin-right: 16px;
  transition: 0.25s ease-in-out;
}
.socials li:hover {
  transform: scale(1.05);
}
.socials li:last-child {
  margin-right: 0;
}
.socials a {
  display: flex;
}

@media screen and (max-width: 768px) {
  .socials li img {
    width: 30px;
    height: 30px;
  }
}
.header {
  position: relative;
  z-index: 10;
  padding-top: 40px;
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  display: flex;
}
.header__nav ul {
  display: flex;
}
.header__nav li {
  margin-right: 10px;
}
.header__nav li:last-child {
  margin-right: 0;
}

@media screen and (max-width: 1024px) {
  .header {
    padding-top: 10px;
  }
  .header__logo img {
    width: 160px;
  }
  .header__nav ul {
    display: block;
  }
  .header__nav li {
    margin-right: 0;
    text-align: center;
  }
  .header__nav li:last-child {
    margin-right: 0;
  }
  .header__nav a {
    display: block;
    padding: 12px 0;
    font-size: 20px;
  }
}
.hero__content {
  position: absolute;
  left: 0;
  bottom: 23px;
}
.hero__title {
  margin-bottom: 35px;
  font-weight: 700;
  font-size: 64px;
  line-height: 1;
  text-transform: uppercase;
}
.hero__inner {
  position: relative;
  min-height: 600px;
}
.hero__img {
  position: relative;
  display: flex;
  justify-content: center;
  margin-top: -90px;
  /* stylelint-disable-next-line number-max-precision */
  padding-bottom: 70.0909090909%;
}
.hero__img img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 1024px) {
  .hero__title {
    font-size: 50px;
  }
}
@media screen and (max-width: 768px) {
  .hero__img {
    margin-top: 0;
  }
  .hero__inner {
    padding-top: 60px;
    padding-bottom: 120px;
    min-height: 500px;
  }
  .hero__title {
    margin-bottom: 15px;
    font-size: 36px;
  }
}
.what {
  position: relative;
  padding: 93px 0 163px;
  background: url("../img/what.jpg") center/cover no-repeat;
}
.what::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 73.73%, #000 90.46%), linear-gradient(0deg, rgba(0, 0, 0, 0) 93.82%, #000 101.61%), rgba(0, 0, 0, 0.3);
}
.what__inner {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  height: 920px;
  min-height: 100vh;
}
.what__top {
  text-align: right;
}
.what__title--top {
  margin-bottom: 15px;
}
.what__bottom {
  margin-top: auto;
}

@media screen and (max-width: 1280px) {
  .what {
    padding: 0;
  }
  .what__inner {
    padding: 75px 0;
    height: auto;
  }
}
@media screen and (max-width: 575px) {
  .what::before {
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 73.73%, #000 90.46%), linear-gradient(0deg, rgba(0, 0, 0, 0) 93.82%, #000 101.61%), rgba(0, 0, 0, 0.7);
  }
  .what__bottom {
    text-align: center;
  }
  .what__top {
    text-align: center;
  }
  .what__title--top {
    margin-bottom: 5px;
  }
}
.vision {
  position: relative;
  padding-bottom: 65px;
  background: url("../img/vision-bg.png") center/cover no-repeat;
}
.vision::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 76.55%, #000 96.27%), linear-gradient(360deg, rgba(0, 0, 0, 0) 84.91%, #000 101.61%), rgba(0, 0, 0, 0.8);
