  }
}
.last {
  position: relative;
  padding: 95px 0 180px;
  background: url("../img/last-bg.png") right -30px/cover no-repeat;
}
.last::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 76.55%, #000 96.27%), linear-gradient(360deg, rgba(0, 0, 0, 0) 84.91%, #000 101.61%), rgba(0, 0, 0, 0.8);
